import React, { Component, Fragment } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import Header from "./components/Header";
import Article from "./components/Article";
import { ArticleList, CategoryList } from "./components/List";
import axios from "axios";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      article: {},
      categories: []
    };
  }
  componentWillMount() {
    axios
      .get("/article/articles.json")
      .then(response => {
        this.setState({ article: response.data });
        const bak = response.data;
        var tmp = new Set();
        var lis = [];
        for (var key in bak) {
          const cates = bak[key][2];
          for (var c in cates) {
            const id = Object.keys(cates[c])[0];
            if (!tmp.has(id)) {
              tmp.add(id);
              lis.push(cates[c]);
            }
          }
        }
        this.setState({ categories: lis });
      })
      .catch(e => {
        console.log(e);
      });

    // document.title = "";
  }
  render() {
    return (
      <div className="App">
        <Router>
          <Fragment>
            <Header />
            <div class="content">
              <Route
                exact
                path="/"
                component={props => (
                  <ArticleList {...props} article={this.state.article} />
                )}
              />
              <Route
                exact
                path="/category"
                component={props => (
                  <CategoryList {...props} categories={this.state.categories} />
                )}
              />
              <Route
                exact
                path="/category/:wc"
                component={props => (
                  <ArticleList {...props} article={this.state.article} />
                )}
              />
              <Route path="/article/:id" component={Article} />
            </div>
          </Fragment>
        </Router>
      </div>
    );
  }
}

export default App;
