import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
// import _ from 'lodash';

import "../sass/Header.sass";
import LogoImg from "../logo-f.svg" 

var delta = 5;

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastScrollTop: 0,
      navClass: "nav-up",
      navbarHeight: 0
    };
  }

  componentDidMount() {
    this.setState({
      navbarHeight: document.getElementsByClassName("header")[0].offsetHeight
    });
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  getDocHeight = () => {
    return Math.max(
      document.body.scrollHeight,
      document.documentElement.scrollHeight,
      document.body.offsetHeight,
      document.documentElement.offsetHeight,
      document.body.clientHeight,
      document.documentElement.clientHeight
    );
  };

  hasScrolled = () => {
    const st = window.scrollY;

    if (Math.abs(this.state.lastScrollTop - st) <= delta) return;

    if (st > this.state.lastScrollTop && st > this.state.navbarHeight) {
      this.setState({ navClass: "nav-down" });
    } else {
      if (st < this.getDocHeight()) {
        this.setState({ navClass: "nav-up" });
      }
    }

    this.setState({ lastScrollTop: st });
  };

  handleScroll = event => {
    this.hasScrolled();
  };

  render() {
    return (
      <div className={`header ${this.state.navClass}`}>
        <div className="button_small_broad_pad" >
          <img src={LogoImg} className="icon"/>
        </div>
        <div className="button_small_broad">
          <div className="button button_small">
            <Link to="/">文章</Link>
          </div>
          <div className="button button_small">
            <Link to="/category">分類</Link>
          </div>
          <div className="button button_small">
            <Link to="/article/about">關於</Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
