import React, { Component, Fragment } from "react";
import Remarkable from "remarkable";
import hljs from "highlight.js";
import axios from "axios";

import "highlight.js/styles/obsidian.css";
import "../sass/Article.sass";

var md = new Remarkable({
  highlight: function(str, lang) {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return `<div class="hljs my_hljs">${
          hljs.highlight(lang, str).value
        }</div>`;
      } catch (err) {}
    }

    try {
      return `<div class="hljs my_hljs">${hljs.highlightAuto(str).value}</div>`;
    } catch (err) {}

    return ""; // use external default escaping
  }
});

class _Article extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: ""
    };
  }

  getArticle = () => {
    axios
      .get(`/article/${this.props.Id}.md`)
      .then(response => {
        console.log();
        this.setState({ content: md.render(response.data) });
      })
      .catch(e => {
        console.log(e);
        this.setState({ content: "<h1>找不到文章 (´･_･`)</h1>" });
      });
  };

  componentWillMount() {
    this.getArticle();
  }
  componentDidUpdate(prevProps) {
    // console.log(prevProps.Id, this.props.Id);
    if (prevProps.Id !== this.props.Id) {
      this.getArticle();
    }
  }
  render() {
    return (
      <div
        className="md"
        dangerouslySetInnerHTML={{ __html: this.state.content }}
      />
    );
  }
}

class Article extends Component {
  render() {
    return <_Article Id={this.props.match.params.id} />;
  }
}

export default Article;
