import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import "../sass/List.sass";

class Title extends Component {
  constructor(props) {
    super(props);
  }
  handleSubmit = () => {
    this.props.history.push(`/article/${this.props.Id}`);
  };
  render() {
    const { chineseTitle, englishTitle, categories, wc } = this.props;
    var inCategory = false;
    categories.map((cata, i) => {
      const currCata = Object.keys(cata)[0];
      if (currCata == wc) inCategory = true;
    });
    if(!wc) inCategory = true;

    if (inCategory) {
      return (
        <div className="button button_header" onClick={this.handleSubmit}>
          <div className="left">
            <h2>{chineseTitle}</h2>
            <h3 className="eng">{`"${englishTitle}"`}</h3>
          </div>
          <div className="right">
            <div className="span">
              <p>
                {categories.map((cata, i) => {
                  return `${i > 0 ? "、" : ""}#${cata[Object.keys(cata)[0]]}`;
                })}
              </p>
            </div>
          </div>
        </div>
      );
    } else return null;
  }
}

export class ArticleList extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { article } = this.props;
    const wc = this.props.match.params.wc;
    return (
      <div className="title_broad">
        {Object.keys(article).map(id => {
          const content = article[id];
          return (
            <Title
              Id={id}
              history={this.props.history}
              chineseTitle={content[0]}
              englishTitle={content[1]}
              categories={content[2]}
              wc={wc}
            />
          );
        })}
      </div>
    );
  }
}

class Category extends Component {
  constructor(props) {
    super(props);
  }
  handleSubmit = () => {
    this.props.history.push(`/category/${this.props.Id}`);
  };
  render() {
    const { title } = this.props;

    return (
      <div class="button button_class" onClick={this.handleSubmit}>
        <span>
          <h4>{`#${title}`}</h4>
        </span>
      </div>
    );
  }
}

export class CategoryList extends Component {
  render() {
    return (
      <div class="button_class_broad">
        {this.props.categories.map(number => {
          const id = Object.keys(number)[0];
          return (
            <Category Id={id} history={this.props.history} title={number[id]} />
          );
        })}
      </div>
    );
  }
}
